.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;
}

.login-card {
  background: white;
  border-radius: 12px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  width: 100%;
  max-width: 420px;
  overflow: hidden;
}

.login-header {
  background: linear-gradient(135deg, #1f2937 0%, #111827 100%);
  color: white;
  padding: 40px 30px;
  text-align: center;
}

.login-header h1 {
  font-size: 32px;
  font-weight: 700;
  margin: 0 0 10px 0;
}

.login-header p {
  font-size: 14px;
  opacity: 0.9;
  margin: 0;
}

.login-form {
  padding: 30px;
}

.btn-block {
  width: 100%;
  margin-top: 10px;
}

.password-field {
  position: relative;
  display: flex;
  align-items: center;
}

.password-field .form-input {
  padding-right: 44px;
}

.password-toggle {
  position: absolute;
  right: 10px;
  background: transparent;
  border: none;
  padding: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #6b7280;
  transition: color 0.2s ease;
}

.password-toggle:hover:not(:disabled),
.password-toggle:focus-visible {
  color: #374151;
}

.password-toggle svg {
  width: 22px;
  height: 22px;
}

.password-toggle:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

.login-footer {
  background-color: #f9fafb;
  padding: 20px 30px;
  border-top: 1px solid #e5e7eb;
}

.login-footer p {
  font-size: 12px;
  color: #6b7280;
  margin: 0 0 10px 0;
  font-weight: 600;
}

.login-footer ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.login-footer li {
  font-size: 11px;
  color: #6b7280;
  padding: 3px 0;
}

.login-footer strong {
  color: #374151;
}
